html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img,
fieldset,
a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input,
button {
  margin: 0;
  padding: 0;
  border: 0;
}

div,
input,
textarea,
select,
button,
h1,
h2,
h3,
h4,
h5,
h6,
a,
span,
a:focus {
  outline: none;
}

ul,
ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

@font-face {
  font-family: 'circe-bold';
  src: url("../fonts/Circe-Bold.woff") format("woff"), url("../fonts/Circe-Bold.ttf") format("truetype");
  font-weight: bold;
  font-style: normal;
}

@font-face {
  font-family: 'circe-regular';
  src: url("../fonts/Circe-Regular.woff") format("woff"), url("../fonts/Circe-Regular.ttf") format("truetype");
  font-weight: bold;
  font-style: normal;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  min-width: 320px;
  width: 100%;
  height: 100%;
  font-size: 1.6rem;
  background-color: #000;
  font: normal 1.6rem/1.33 "circe-regular", sans-serif;
}

.main-header {
  min-height: 60rem;
  height: 100vh;
  background-color: #000;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.main-header__logo svg {
  width: 25rem;
  height: auto;
}

.main-header__promo {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 90rem;
  margin-top: 5rem;
}

.main-header__btn {
  width: 34rem;
  margin: 10rem auto 0;
}

.main-header__socials {
  margin-top: auto;
  margin-left: auto;
  margin-right: 4rem;
  padding-bottom: 5rem;
}

.promo {
  text-align: center;
  display: flex;
  flex-direction: column;
}

.promo__title {
  text-align: center;
  color: #eea30c;
  font-size: 9rem;
  font-family: "circe-bold", sans-serif;
  text-transform: uppercase;
}

.btn {
  text-align: center;
  text-decoration: none;
  color: #fff;
  display: block;
  padding: 1rem;
  text-transform: uppercase;
  font-size: 2.5rem;
  font-family: "circe-regular", sans-serif;
  border: 2px solid #fff;
  border-radius: 10px;
  transition: .25s opacity linear;
}

.btn:hover {
  opacity: .4;
}

.socials {
  list-style-type: none;
}

.socials__item {
  margin-right: 1rem;
  display: inline-block;
}

.social {
  display: block;
  width: 50px;
  height: 50px;
  transition: .25s opacity linear;
  background-size: cover;
}

.social:hover {
  opacity: .8;
}

.social--facebook {
  background-image: url(../images/facebook.svg);
}

.social--instagram {
  background-image: url(../images/instagram.svg);
}

@media only screen and (max-width: 1024px) {
  html {
    font-size: 62.5%;
  }
}

@media only screen and (max-width: 992px) {
  html {
    font-size: 50%;
  }

  .main-header__promo {
    width: 80%;
  }

  .social {
    width: 40px;
    height: 40px;
  }
}

@media only screen and (max-width: 768px) {
  .promo__title {
    font-size: 7rem;
  }
}

@media only screen and (max-width: 576px) {
  .main-header__logo {
    width: 30rem;
    height: 30rem;
    text-align: center;
    margin: 0 auto;
  }

  .main-header__logo svg {
    width: 30rem;
    height: 30rem;
  }

  .main-header__promo {
    width: 100%;
    margin-top: 1rem;
  }

  .main-header__btn {
    width: 30rem;
  }

  .promo__title {
    font-size: 5rem;
  }

  .btn {
    font-size: 2rem;
  }
}

@media only screen and (max-width: 552px) {
  .social {
    width: 30px;
    height: 30px;
  }
}

@media only screen and (max-width: 320px) {
  .main-header__btn {
    width: 25rem;
  }

  .main-header__socials {
    margin: auto auto 5rem;
  }

  .btn {
    font-size: 1.8rem;
  }
}