@import 'helpers/all';
@font-face {
  font-family: 'circe-bold';
  src:  url('../fonts/Circe-Bold.woff') format('woff'), url('../fonts/Circe-Bold.ttf')  format('truetype');
  font-weight: bold;
  font-style: normal; }
@font-face {
  font-family: 'circe-regular';
  src:  url('../fonts/Circe-Regular.woff') format('woff'), url('../fonts/Circe-Regular.ttf')  format('truetype');
  font-weight: bold;
  font-style: normal; }


html {
  box-sizing: border-box;
  font-size: 62.5%;
  @include r(1024) {
    font-size: 62.5%; }
  @include r(992) {
    font-size: 50%; } }
*, *:before, *:after {
  box-sizing: inherit; }
body {
  min-width: 320px;
  width: 100%;
  height: 100%;
  font-size: 1.6rem;
  background-color: #000;
  font: normal 1.6rem/1.33 "circe-regular", sans-serif; }
.main-header {
  min-height: 60rem;
  height: 100vh;
  background-color: #000;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  &__logo {
    svg {
      width: 25rem;
      height: auto; }
    @include r(576) {
      width: 30rem;
      height: 30rem;
      text-align: center;
      margin: 0 auto;
      svg {
        width: 30rem;
        height: 30rem; } } }
  &__promo {
    @include vcenter;
    width: 90rem;
    margin-top: 5rem;
    @include r(992) {
      width: 80%; }
    @include r(576) {
      width: 100%;
      margin-top: 1rem; } }
  &__btn {
    width: 34rem;
    margin: 10rem auto 0;
    @include r(576) {
      width: 30rem; }
    @include r(320) {
      width: 25rem; } }
  &__socials {
    margin-top: auto;
    margin-left: auto;
    margin-right: 4rem;
    padding-bottom: 5rem;
    @include r(320) {
      margin: auto auto 5rem; } } }

.promo {
  text-align: center;
  display: flex;
  flex-direction: column;

  &__title {
    text-align: center;
    color: #eea30c;
    font-size: 9rem;
    font-family: "circe-bold", sans-serif;
    text-transform: uppercase;
    @include r(768) {
      font-size: 7rem; }
    @include r(576) {
      font-size: 5rem; }
    //+r(320)
 } }    //font-size: 4rem
.btn {
  text-align: center;
  text-decoration: none;
  color: #fff;
  display: block;
  padding: 1rem;
  text-transform: uppercase;
  font-size: 2.5rem;
  font-family: "circe-regular", sans-serif;
  border: 2px solid #fff;
  border-radius: 10px;
  transition: .25s opacity linear;
  &:hover {
    opacity: .4; }
  @include r(576) {
    font-size: 2rem; }
  @include r(320) {
    font-size: 1.8rem; } }

.socials {
  list-style-type: none;
  &__item {
    margin-right: 1rem;
    display: inline-block; } }
.social {
  display: block;
  width: 50px;
  height: 50px;
  transition: .25s opacity linear;
  background-size: cover;
  @include r(992) {
    width: 40px;
    height: 40px; }
  @include r(552) {
    width: 30px;
    height: 30px; }
  &:hover {
    opacity: .8; }
  &--facebook {
    background-image: url(../images/facebook.svg); }
  &--instagram {
    background-image: url(../images/instagram.svg); } }
